    clearAllTagSelected() {
      this.tagList.map(item => {
        item.selected = false
      })
      this.backfillTagListToRowsInfo() // tagrowsInfo
    },

    //#endregion tag 

    //#region tag 

    // 
    tagblurHandle() {
      /*
       tag 
       
       tag tagblur
       
      */
      this.autoCloseTagMenu = true
      // 
      setTimeout(() => {
        if (this && this.tagMenu_close && this.autoCloseTagMenu) {
          try {
            this.tagMenu_close()
          } catch (e) {
            console.log('tagblurHandle error', e)
          }
        }
