      console.log("tag", data);
      let { tag, triggerIndex, target } = data;

      switch (target.label) {
        case "tag": //
          this.ganTT2Option.showSelected = true;
          break;
        case "tag": //
          this.ganTT2Option.showSelected = false;
          break;
        case "tag": //
